import Vue from "vue";
import Router from "vue-router";
import User from "./components/Users/User.vue"
import AddUser from "./components/Users/AddUser";
import UserList from "./components/Users/Users";
import Login from "./components/Login";
import Register from "./components/Register";

console.log(Vue);
Vue.use(Router);

export default new Router({
  mode: "history",
  routes: [
    {
      path: "/users/:id",
      name: "user-details",
      component: User
    },
    {
      path: "/add",
      name: "add",
      component: AddUser
    },
    {
      path: "/users",
      name: "users",
      component: UserList
    },
    {
      path: "/login",
      name: "login",
      component: Login
    },
    {
      path: "/register",
      name: "register",
      component: Register
    }
  ]
});