























import http from "./../http-common";

  export default {
      
    data(){
      return {
        email : "",
        password : ""
      }
    },
    methods : {
      handleSubmit(e){
        e.preventDefault()
        if (this.password.length > 0) {
           http.post("/account/login", {
            Email: this.email,
            Password: this.password
          })
          .then(response => {
              console.log(response);
              this.$router.push('users')
          })
          .catch(function (error) {
            console.error(error.response);
          });
        }
      }
    }
  }
